: 1;\n\n  padding: 15px 10px 15px 10px;\n  background-color: var(--primary);\n}\n.forecast.hidden {\n  display: none;\n}\n.forecast {\n  display: block;\n}\n.form {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: var(--white);\n  width: 226px;\n  height: 25px;\n  border-radius: 8px;\n  border: none;\n  outline: none;\n}\n.search-row {\n  display: flex;\n  gap: 10px;\n  align-items: center;\n  justify-content: center;\n  justify-self: center;\n  margin-bottom: 15px;\n}\n.search {\n  width: 200px;\n  height: 25px;\n  border-radius: 8px;\n  border: none;\n  outline: none;\n  padding: 2px 5px 2px 5px;\n  justify-content: center;\n}\n.search.btn {\n  width: 25px;\n  height: 25px;\n  border-radius: 50px;\n  background-color: var(--white);\n  right: 0px;\n  cursor: pointer;\n}\n.toggle-container {\n  position: relative;\n  width: 50px;\n  height: 25px;\n  background-color: var(--white);\n  border-radius: 12px;\n}\n.toggle-btn {\n  width: 25px;\n  height: 25px;\n  background-color: transparent;\n  border-radius: 12px;\n  border: 2px solid black;\n  transition: all 0.2s ease;\n}\n\n.toggle-btn.active {\n  transform: translateX(100%);\n}\n\n.toggle-container::before {\n  content: \"C°\";\n  position: absolute;\n  left: 6px;\n  top: 52%;\n  transform: translateY(-50%);\n  transition: all 0.3s;\n}\n.toggle-container::after {\n  content: \"F°\";\n  position: absolute;\n  right: 4px;\n  top: 52%;\n  transform: translateY(-50%);\n  transition: all 0.3s;\n}\n\n.error-text {\n  align-self: center;\n  min-height: 1.2rem;\n  margin-bottom: 10px;\n}\n\n.today-weather {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 5px;\n  justify-content: center;\n  align-items: center;\n  gap: 10px;\n  margin-bottom: 25px;\n}\n\n.today-block {\n  position: relative;\n  display: flex;\n  width: 400px;\n  height: 260px;\n  border-radius: 10px;\n  overflow: hidden;\n  box-shadow: var(--shadow);\n}\n\n.today-content {\n  position: absolute;\n  height: 100%;\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  background-color: transparent;\n  border-radius: 10px;\n  padding: 15px;\n  color: var(--white);\n  transition: all 0.3s ease-in;\n}\n.today-content img.svg:not(.today-icon) {\n  filter: invert(100%);\n}\n.today-image {\n  position: absolute;\n  height: 100%;\n  width: 100%;\n  object-fit: cover;\n  filter: brightness(70%);\n}\n\n.today-row {\n  display: flex;\n  align-items: center;\n  gap: 5px;\n  margin-bottom: 10px;\n  margin-left: 20px;\n}\n.today-row:last-of-type {\n  margin-bottom: 0px;\n}\n.today-row:first-of-type {\n  margin-left: 0px;\n}\n.today-icon {\n  width: 4rem;\n  height: 4rem;\n}\n.location {\n  line-height: 1.2;\n  font-size: 1.5rem;\n  height: 3.5rem;\n  margin-bottom: 5px;\n  overflow: hidden;\n  display: -webkit-box;\n  line-clamp: 2;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n  text-overflow: ellipsis;\n}\n.location-icon {\n  align-self: flex-start;\n  width: 1.8rem;\n  height: 1.8rem;\n  margin-right: 5px;\n}\n.temp-now {\n  font-size: 2rem;\n  width: 60px;\n}\n.description {\n  height: 3rem;\n}\n\n.weather-details {\n  display: flex;\n  align-items: center;\n  gap: 5px;\n  margin-right: 30px;\n}\n.weather-details img {\n  width: 1.1rem;\n  height: 1.1rem;\n}\n\n.sun {\n  width: 200px;\n  height: 260px;\n  background-color: var(--white);\n  border-radius: 10px;\n  box-shadow: var(--shadow);\n}\n.sunrise-details,\n.sunset-details {\n  position: relative;\n  font-size: 1.2rem;\n  height: 50%;\n  display: flex;\n  gap: 0px;\n  flex-direction: column;\n  justify-content: center;\n  color: var(--white);\n}\n.sunrise-details img,\n.sunset-details img {\n  object-fit: cover;\n  height: 100%;\n  filter: brightness(70%);\n}\n.sunrise-details img {\n  border-radius: 10px 10px 0px 0px;\n}\n.sunset-details img {\n  border-radius: 0px 0px 10px 10px;\n}\n.sun-text {\n  font-weight: 200;\n  font-size: 1rem;\n}\n.sunrise-content,\n.sunset-content {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  translate: -50% -50%;\n  display: flex;\n  align-items: center;\n  flex-direction: column;\n}\n.carousel-container {\n  justify-self: center;\n  align-self: center;\n  width: 400px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n.today-carousel {\n  background-color: var(--white);\n  justify-self: center;\n  width: 400px;\n  overflow: hidden;\n  margin-bottom: 20px;\n  position: relative;\n  overflow: hidden;\n  border-radius: 10px;\n  box-shadow: var(--shadow);\n}\n.carousel-items {\n  background-color: var(--white);\n  display: flex;\n}\n\n.next.btn,\n.prev.btn {\n  background-color: rgba(255, 255, 255, 0.226);\n  width: 30px;\n  height: 30px;\n  cursor: pointer;\n  border: none;\n  border-radius: 15px;\n  cursor: pointer;\n  position: absolute;\n  top: 50%;\n  transform: translateY(-50%);\n}\n.prev.btn {\n  left: 5px;\n}\n.next.btn {\n  right: 5px;\n}\n.next.btn:hover,\n.prev.btn:hover {\n  background-color: rgba(211, 211, 211, 0.833);\n}\n\n.hour-block {\n  display: flex;\n  flex-direction: column;\n  width: 50px;\n  padding: 10px;\n  align-items: center;\n}\n.hour-icon.svg {\n  width: 2.2rem;\n  height: 2.2rem;\n}\n\n.forecast-container {\n  justify-self: center;\n  align-self: center;\n  width: 400px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n.weather-header {\n  margin-bottom: 10px;\n  align-self: flex-start;\n}\n.forecast-block {\n  justify-self: center;\n  width: 400px;\n  background-color: var(--white);\n  border-radius: 10px;\n  padding: 15px;\n  box-shadow: var(--shadow);\n}\n\n.day-row {\n  display: flex;\n  justify-content: space-between;\n  padding: 5px;\n}\n.day-row div {\n  width: 60px;\n  text-align: left;\n  display: flex;\n  align-items: center;\n  gap: 5px;\n}\n.day-row div:last-of-type {\n  width: 90px;\n}\n.day-row div img {\n  width: 1.1rem;\n  height: 1.1rem;\n}\n.weather-icon {\n  width: 2rem;\n  height: 2rem;\n}\n.temp-container p:first-of-type {\n  margin-right: 15px;\n}\n.temp-container {\n  justify-content: space-between;\n}\n.footer {\n  min-height: 60px;\n  padding: 0px 15px 0px 15px;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n.loader {\n  width: 50px;\n  height: 10px;\n  aspect-ratio: 4;\n  background: radial-gradient(circle closest-side, #000 90%, #0000) 0 /\n    calc(100% / 3) 100% space;\n  clip-path: inset(0 100% 0 0);\n  animation: l1 0.5s steps(4) infinite;\n}\n@keyframes l1 {\n  to {\n    clip-path: inset(0 -34% 0 0);\n  }\n}\n"],"sourceRoot":""}]);
